<template>
  <div class="my">
    <div
      class="header"
      :style="{
        'background-image':
          'url(' +
          $store.state.finditemsdata.banner.list[
            randomNumber(0, $store.state.finditemsdata.banner.list.length - 1)
          ].image +
          ')'
      }"
    >
      <div class="head-portrait">
        <img
          src="https://static.kaiyanapp.com/eyepetizer-web/assets/images/index/home_bk_3.7819585d.jpg"
          alt=""
          @click="PortPrait"
        />
      </div>
      <div v-if="$store.state.ishasuser" class="register" @click="PortPrait">
        点击登录
      </div>
      <div v-else class="register" @click="PortPrait">
        {{ $store.state.user.tel }}
      </div>
    </div>
    <div class="xuanze">
      <van-cell is-link @click="getMeLick">
        <template slot="title">
          <van-icon name="like-o" />
          <span class="custom-title">我的喜欢</span>
        </template>
      </van-cell>
      <van-cell is-link>
        <template slot="title">
          <van-icon name="down" />
          <span class="custom-title">离线缓存</span>
        </template>
      </van-cell>
      <van-cell is-link @click="goHistory">
        <template slot="title">
          <van-icon name="clock-o" />
          <span class="custom-title">播放历史</span>
        </template>
      </van-cell>
      <van-cell is-link>
        <template slot="title">
          <van-icon name="chat-o" />
          <span class="custom-title">我的消息</span>
        </template>
      </van-cell>
      <van-cell is-link @click="setTab">
        <template slot="title">
          <van-icon name="setting-o" />
          <span class="custom-title">设置</span>
        </template>
      </van-cell>
      <van-cell is-link>
        <template slot="title">
          <van-icon name="edit" />
          <span class="custom-title">问题反馈</span>
        </template>
      </van-cell>
    </div>
  </div>
</template>

<script>
export default {
  name: "My",
  data() {
    return {
      ishasuser: true,
      user: null
    };
  },
  methods: {
    //设置
    setTab() {
      window.console.log("设置");
      if (!this.$store.state.ishasuser) {
        this.$router.push("/portrait");
        this.$store.commit("submitSet", true);
      }
    },
    //随机背景
    randomNumber(m, n) {
      return Math.round(Math.random() * (n - m) + m);
    },
    //登录
    PortPrait() {
      let user = JSON.parse(window.localStorage.getItem("user"));
      this.$store.commit("submitUser", user);
      if (!user) {
        this.$router.push("/portrait");
      } else {
        this.$store.commit("submitIshasuser", false);
      }
    },
    //进入我的喜欢
    getMeLick() {
      let user = JSON.parse(window.localStorage.getItem("user"));
      // this.$store.commit("submitUser", user);
      if (!user) {
        this.$router.push("/portrait");
      } else {
        this.$router.push("/melike");
      }
    },
    //进入历史记录
    goHistory() {
      window.console.log("xxx");
      let user = JSON.parse(window.localStorage.getItem("user"));
      // this.$store.commit("submitUser", user);
      if (!user) {
        this.$router.push("/portrait");
      } else {
        this.$router.push("/history");
      }
    }
  },

  created() {
    let user = JSON.parse(window.localStorage.getItem("user"));
    // window.console.log(user);
    this.$store.commit("submitUser", user);
    if (user != null) {
      this.$store.commit("submitIshasuser", false);
    }
  }
};
</script>

<style lang="less">
.my {
  height: 100vh;
  overflow: hidden;
  width: 100%;
  background: rgb(26, 24, 24);
  vertical-align: middle;
  display: flex;
  flex-direction: column;
  .header {
    height: 30vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-repeat: no-repeat;
    background-size: cover;
    .head-portrait {
      img {
        width: 60px;
        height: 60px;
        border-radius: 50%;
      }
    }
    .register {
      color: white;
      font-size: 14px;
      padding: 15px 0;
    }
  }
  .xuanze {
    flex: 1;
    .van-cell {
      color: white;
      background: rgb(32, 31, 31);
      border: none;
      border-top: 1px solid rgb(41, 40, 40);
      &::after{
        border: none;
      }
      .van-icon {
        padding: 0px 15px 0 0;
      }
      &:nth-of-type(5) {
        margin-top: 35px;
      }
    }
  }
}
</style>
